import { AppRoutingModule } from './routing-module';
import { HomePageComponent } from './home-page/home-page.component';
import { ForexComponent } from './forex/forex.component';

@NgModule({
  declarations: [
    AppComponent,
    WeatherComponent,
    TranslateComponent,
    NewsComponent,
    MapsComponent,
    HomePageComponent,
    ForexComponent
  ],
  imports: [
    BrowserModule,FormsModule,HttpModule,AppRoutingModule,AgmCoreModule.forRoot({
      apiKey: 'AIzaSyBR07XK6MULjzRSEwvrHiA2FZEnI8dtJAU'
    }),MdToolbarModule,MdTabsModule,MaterialModule,MdSelectModule,MdInputModule,MdButtonModule,MdGridListModule,MdCheckboxModule,BrowserAnimationsModule
  ],
  schemas:[CUSTOM_ELEMENTS_SCHEMA],
  providers: [WeatherService,ApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



